import React, { useEffect, useState }from 'react'
import {Link, } from 'react-router-dom'
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { IconButton } from '@material-ui/core';
import Profile from './Profile'
import '../assets/Editor.css'
import AutorenewIcon from '@material-ui/icons/Autorenew';
import ImageIcon from '@material-ui/icons/Image';
import * as auth_actions from '../store/actions/auth_action'
import axios from 'axios'
const Editor = () => {
    const [formData, setFormData] = useState({
        image:[],
    })

    // useEffect(() => {
    //     axios.get(``)
    // })

    function drophandler(e) {
        console.log(e)
        if (e.target.value) {
            console.log(e.target.files[0].name)
            setFormData({...formData, image: e.target.files[0].name})
        } else {
        }
    
    }

    return (
        <div className='edit'>
            <div className='edit_header'>
                <Link to='/setting'>
                    <IconButton style={{outline:'none'}}>
                        <ArrowForwardIosIcon />
                    </IconButton>
                </Link>
            </div>

            <div className='edit_content'>
                <Profile/>
                <div className='edit_setting'>
                    <div className='edit_container'>
                        {/* <div className='edit_title'> */}
                        <h1>Setting</h1>
                        {/* </div> */}
                        <form className='edit_form'>
                            <input placeholder='name' type='text' className='edit_input'/>
                            <input placeholder='age' type="number" className='edit_input'/>
                            <input placeholder='gender' className='edit_input'/>
                            <input placeholder='you speak ...' className='edit_input'/>
                            <input placeholder='you want to learn ...' className='edit_input'/>
                            <input placeholder='your profile message' type='text' className='edit_input'/>
                            <input placeholder='password' type="password" className='edit_input'/>
                            <label className='edit_input_file'>
                                {formData.image.length > 0 ? <p1><AutorenewIcon className='edit_icon'/>{formData.image}</p1>  
                                : <p1><ImageIcon className='edit_icon'/>choose your image</p1>}
                            <input placeholder='image' type='file' className='edit_input' accept="image/png, image/jpeg"　onChange={drophandler}/>
                            </label>

                            <select className='edit_select'>
                                <option>Monday</option>
                                <option>Tuesday</option>
                                <option>Wednesday</option>
                                <option>Thursday</option>
                                <option>Friday</option>
                                <option>Saturday</option>
                                <option>Sunday</option>
                            </select>
                            <div className='edit_time'>
                            <input type='time' className='edit_input_time'/>
                            ~
                            <input type='time' className='edit_input_time' />
                            </div>
                        </form>
                        </div>
                    <Link to='/setting'>
                        <button className='edit_button' onfocus="this.blur();"><span>SAVE</span></button>
                    </Link>
                </div>
            </div>
       </div>
    )
}

const mapstateToProps = state => {
    return {
    isAuthenticated: state.auth_reducer.isAuthenticated
}
}

