import * as actionType from './actionType'
import axios from 'axios'

export const Create = (name, native_lan, foreign_lan) => async dispatch => {
    const config = {
        headers: {
        'Content-Type': 'application/json'
        }
    }
    const body = JSON.stringify({name, native_lan, foreign_lan})
    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/myprofile/create/`, body, config)
        dispatch({
            type: actionType.PROFILE_CREATE_SUCCESS,
            payload: res.data
        })
    } catch(err) {
        dispatch({
            type: actionType.PROFILE_CREATE_FAIL
        })
    }
}


export const Receive = (id) => async dispatch => {
    console.log(`${process.env.REACT_APP_API_URL}/myprofile/receive/${id}/`)
    try {
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/myprofile/detail/${id}/`)
        dispatch ({
            type: actionType.PROFILE_RECEIVE_SUCCESS,
            payload: res.data
        })
    } catch(err) {
        dispatch({
            type:actionType.PROFILE_RECEIVE_FAIL,
        })
    }
}

export const Update = (id, name, age, gender, native_lan, foreign_lan, image, location, time_start, time_end) => async dispatch => {
    try {
        const config = {
            headers: {'Content-Type': 'application/json',
            'Accept': 'application/json'
        }}
        const body = JSON.stringify({name, age, gender, native_lan, foreign_lan, image, location, time_start, time_end})
        const res = await axios.put(`${process.env.REACT_APP_API_URL}/myprofile/update/${id}/`, body, config)
        dispatch({
            type: actionType.PROFILE_UPDATE_SUCCESS,
            payload: res.data
        })
    } catch(err) {
        dispatch({
            type: actionType.PROFILE_UPDATE_FAIL,

        })
    }
}

export const Delete = (id) => async dispatch => {
    try {
        const res = await axios.delete(`${process.env.REACT_APP_API_URL}/myprofile/delete/${id}/`)
        dispatch({
            type: actionType.PROFILE_DELETE_SUCCESS,
        })
    } catch(err) {
        dispatch({
            type: actionType.PROFILE_DELETE_FAIL
        })
    }
}

